<template>
    <scroll-view class="sidebar" scroll-y="true">
        <uni-data-menu :localdata="localMenus" :unique-opened="true" :active="activeUrl" active-text-color="#409eff"
            @select="select"></uni-data-menu>
    </scroll-view>
</template>

<script>
    export default {
        data() {
            return {
                activeUrl: '',
                localMenus: [{
                    menu_id: "demo",
                    text: '静态功能演示',
                    icon: 'uni-icons-list',
                    value: "",
                    children: [{
                        menu_id: "index",
                        text: '首页',
                        icon: '',
                        value: '/pages/index/index',
                    }, {
                        menu_id: "test",
                        text: '测试',
                        icon: '',
                        value: '/pages/test/test',
                    }]
                }]
            }
        },
        methods: {
            select(e) {
                let url = e.value

                if (!url) {
                    url = this.active
                }
                this.clickMenuItem(url)
            },
            clickMenuItem(url) {
                // #ifdef H5
                if (url.indexOf('http') === 0) {
                	return window.open(url)
                }
                // #endif
                
                // url 开头可用有 / ，也可没有
                if (url[0] !== '/' && url.indexOf('http') !== 0) {
                	url = '/' + url
                }
                uni.redirectTo({
                	url: url
                })
            },
        }
    }
</script>

<style lang="scss">
    .sidebar {
        position: fixed;
        top: var(--top-window-height);
        width: 240px;
        height: calc(100vh - (var(--top-window-height)));
        box-sizing: border-box;
        border-right: 1px solid darken($left-window-bg-color, 8%);
        background-color: $left-window-bg-color;
        padding-bottom: 10px;
    }

    .title {
        margin-left: 5px;
    }
</style>
